:root {
  --white: #fff;
  --black: #000;
  --grey: #f7f7f7;
  --red: #ff001b;
  --accent: #ff6b08;
  --text-color--dark: #000;
  --text-color--bright: #fff;
  --text-color--accent: #ff6b08;
  --secondary-text-color: #545454;
  --light-grey-text-color: #8c8c8c;
  --text-color--address: #d6936d;
  --bg-color--bright: #fff;
  --bg-color--light-grey: #f7f7f7;
  --bg-color--backdrop: rgba(0, 0, 0, .2);
  --bp-mobile: 320px;
  --bp-tablet: 768px;
  --bp-desktop: 1280px;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

